import React, { useState, useEffect } from 'react';
import './CatchMobile.css'; // Mobile-specific styles
import catchHero from './image/hero.png'; // Import your image
import linkdenLogo from './image/linkden.jpg'; // Import LinkedIn logo
import { FaWhatsapp, FaEnvelope, FaMask, FaCheckCircle } from 'react-icons/fa'; // Import react-icons

const CatchMeMobile = () => {
    const [position, setPosition] = useState({ top: '40%', left: '40%' });
    const [hoverText, setHoverText] = useState('CATCH ME');
    const [caught, setCaught] = useState(false);
    const [modalVisible, setModalVisible] = useState(false);
    const [showFeedbackModal, setShowFeedbackModal] = useState(false);
    const [feedback, setFeedback] = useState('');
    const [feedbackSubmitted, setFeedbackSubmitted] = useState(false);

    const handleTouchStart = () => {
        if (!caught) {
            setCaught(true);
            setHoverText('YOU CAUGHT ME!');
            setModalVisible(true);
        }
    };

    const closeModal = () => {
        setModalVisible(false);
        setCaught(false);
        setHoverText('CATCH ME');
    };

    const moveCatchBox = () => {
        if (!caught) {
            const maxTop = window.innerHeight - 150;
            const maxLeft = window.innerWidth - 150;
            const randomTop = Math.random() * maxTop + 'px';
            const randomLeft = Math.random() * maxLeft + 'px';
            setPosition({ top: randomTop, left: randomLeft });
        }
    };

    useEffect(() => {
        if (!caught) {
            const intervalId = setInterval(moveCatchBox, 500);
            return () => clearInterval(intervalId);
        }
    }, [caught]);

    const handleEmailClick = () => {
        const email = "contact@abdelkodoushachani.uk";
        const subject = "I Caught You!";
        const body = "Hello! I caught you and would love to connect. Please let me know how I can reach you.";
        window.location.href = `mailto:${email}?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;
    };

    const handleWhatsAppClick = () => {
        const phoneNumber = "+441158883068"; // Replace with your phone number
        const message = "Hello! I caught you and would love to connect.";
        const url = `https://api.whatsapp.com/send?phone=${encodeURIComponent(phoneNumber)}&text=${encodeURIComponent(message)}`;
        window.open(url, "_blank");
    };

    const handleShowFeedbackModal = () => {
        setModalVisible(false); // Close Catch Me Modal
        setShowFeedbackModal(true);
        setFeedbackSubmitted(false);
    };

    const handleCloseFeedbackModal = () => {
        setShowFeedbackModal(false);
        setModalVisible(true); // Reopen Catch Me Modal
    };

    const handleFeedbackSubmit = (e) => {
        e.preventDefault();

        const formData = new FormData();
        formData.append('entry.255952891', feedback); // Replace with your actual field ID from Google Forms

        fetch('https://docs.google.com/forms/d/e/1FAIpQLSdUJhu3l9oYzEYr3Bcn0fk6E3Ccq9ez-bqP3aNPv2ushqnG0w/formResponse', {
            method: 'POST',
            body: formData,
            mode: 'no-cors',
        })
            .then(() => {
                setFeedbackSubmitted(true);
                setFeedback('');
                setTimeout(() => {
                    handleCloseFeedbackModal(); // Close the modal after 2 seconds
                }, 2000);
            })
            .catch((error) => {
                console.error('Error submitting feedback:', error);
                alert('There was an error submitting your feedback. Please try again.');
            });
    };

    return (
        <div className="catch-section-mobile" onTouchMove={(e) => e.preventDefault()}>
            <div
                className="catch-me-container-mobile"
                style={position}
                onTouchStart={handleTouchStart}
            >
                <div className="catch-box">
                    <img src={catchHero} alt="Catch Me" className="catch-image" />
                    <div className="hover-text">{hoverText}</div>
                </div>
            </div>
            {modalVisible && (
                <div className="mb-modal">
                    <div className="mb-modal-content">
                        <span className="close" onClick={closeModal}>&times;</span>
                        <img
                            src={linkdenLogo}
                            alt="LinkedIn Profile"
                            className="profile-pic"
                        />
                        <h2>Hi there! You caught me!</h2>
                        <p>In the future, it won't be so easy to catch me. Stay tuned for updates!</p>
                        <div className="contact-info">
                            <p>Get in touch:</p>
                            <button onClick={handleWhatsAppClick} className="catch-contact-button">
                                <FaWhatsapp className="catch-icon" />
                                Contact via WhatsApp
                            </button>
                            <button onClick={handleEmailClick} className="catch-contact-button">
                                <FaEnvelope className="catch-icon" />
                                Contact via Email
                            </button>
                        </div>
                        <p>Your Voice Matters: Share Your Thoughts Anonymously</p>
                        <button onClick={handleShowFeedbackModal} className="catch-contact-button">
                        <FaMask className="su-icon"   />
                        Anonymous Feedback
                        </button>
                        
                    </div>
                </div>
            )}

            {/* Modal for feedback form */}
            {showFeedbackModal && (
                <div className="f-modal-overlay" onClick={handleCloseFeedbackModal}>
                    <div className="f-modal-content" onClick={(e) => e.stopPropagation()}>
                    <FaMask className="su-icon" size={60}  />
                    <h2>Anonymous Feedback Welcome</h2>
                 
            <p className="feedback-anonymous-text">
            Feel free to share your thoughts anonymously about your experience with me, my website, or our communication.
Your insights drive innovation and improvement. Thank you!

            </p>
                        {!feedbackSubmitted ? (
                            <form onSubmit={handleFeedbackSubmit}>
                                <textarea
                                    value={feedback}
                                    onChange={(e) => setFeedback(e.target.value)}
                                    placeholder="Type your anonymous feedback here..."
                                    rows="3"
                                    required
                                ></textarea>
                                <br />
                                <button type="submit" className="f-modal-submit-button">
                                <FaMask className="su-icon"  />
                                    Anonymous Feedback
                                </button>
                            </form>
                        ) : (
                            <div className="feedback-submitted-message">
                            <FaCheckCircle className="validation-icon" size={40} />
                          Your Feedback Matters 
                          Thank You for Helping Shape My Journey!
                      </div>
                        )}
                        <button className="f-modal-close-button" onClick={handleCloseFeedbackModal}>
                            Close
                        </button>
                    </div>
                </div>
            )}
        </div>
    );
};

export default CatchMeMobile;
